<template>
<div class="container">
  <h2 class="text-center mt-5">Tapşırıq Meneceri</h2>
  <div class="d-flex">
    <input v-model="taski" type="text" class="form-control" id="inp" placeholder="Tapşırığı daxil edin...">
    <button @click="submittask" class="btn btn-warning rounded-0">Yarat</button>
  </div>
  <table class="table table-bordered mt-5" >
  <thead>
    <tr>
      <th scope="col">Tapşırıq</th>
      <th scope="col">Status</th>
      <th scope="col" class="text-center">#</th>
      <th scope="col" class="text-center">#</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(task, index) in tasks" :key="index">
      <td>{{task.name}}</td>
      <td style="width:120px"><span class="pointer" @click="changestatus(index)"
        :class="{'text-danger':task.status === 'yeni',
      'text-warning':task.status === 'natamam',
      'text-success':task.status === 'tamamlanmış'}"
        > 
        {{task.status}}
      </span></td>
      <td><div class="text-center" id="pen" @click="edittask(index)"><span class="fa fa-pen"></span></div></td>
      <td><div class="text-center" id="trash" @click="deletetask(index)"><span class="fa fa-trash" ></span></div></td>
    </tr>

 
  </tbody>
</table>
<p>Qeyd: Tapşırıqların olduğu sətirdə status üzərinə klik edərək status vəziyyətini dəyişə bilərsiniz. </p>
</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return{

      taski:'',
      editedtask:null,
      availableStatuses:['yeni','natamam', 'tamamlanmış'],

      tasks:[
{
  name:'Yeni tapşırıqların yaradılması',
  status:'tamamlanmış',
  
},
{
  name:'Teqlərin təyini üçün input elementi',
  status:'natamam'
}
      ]
    }
  },
  methods:{
submittask(){
 if(this.taski.length === 0)return;

 if(this.editedtask === null){
 this.tasks.push({
  name:this.taski,
  status:'yeni'
 })
}
else{
  this.tasks[this.editedtask].name = this.taski;
  this.editedtask = null;
}
this.taski='';
},
deletetask(index){
  this.tasks.splice(index, 1);
},
edittask(index){
  this.taski= this.tasks[index].name;
  this.editedtask=index;
},
changestatus(index){
  let newIndex = this.availableStatuses.indexOf(this.tasks[index].status);
  if(++newIndex > 2) newIndex = 0;
  this.tasks[index].status = this.availableStatuses[newIndex];
}
  }
}
</script>


<style scoped>

.pointer{
  cursor: pointer;
}
#pen{
  cursor: pointer;
  color: green;
}
#trash{
  cursor: pointer;
  color: red;
}



</style>
